.MyButton_pulse__3zrxw:hover,
.MyButton_pulse__3zrxw:focus {
    -webkit-animation: MyButton_pulse__3zrxw 1s;
    animation: MyButton_pulse__3zrxw 1s;
    box-shadow: 0 0 0 2em rgba(255, 255, 255, 0);
}

@-webkit-keyframes MyButton_pulse__3zrxw {
    0% {
        box-shadow: 0 0 0 0 var(--hover);
    }
}

@keyframes MyButton_pulse__3zrxw {
    0% {
        box-shadow: 0 0 0 0 var(--hover);
    }
}


.MyButton_pulse__3zrxw {
    --color: white;
    --hover: #8200E5;
}

button {
    color: var(--color);
    -webkit-transition: 0.25s;
    transition: 0.25s;
}
button:hover,
button:focus {
    border-color: var(--hover);
    color: #8200E5;
}

button {
    background: none;
    border: 2px solid;
    font: inherit;
    line-height: 1;
    margin: 0.5em;
    padding: 1em 2em;
}
